* {
  box-sizing: border-box;
}

html {
  overflow-x: hidden !important;
}

body {
  position: relative;
  margin: 0;
  padding: 0;
  overflow-x: hidden !important;
  -webkit-font-smoothing: antialiased;
  background-color: var(--color-white);
  color: var(--color-black);
}

p,
li,
a,
small, button {
  color: #131313;
  line-height: 1.2em;
}

p,
li,
a,
figure {
  margin: 0;
  padding: 0;
}

strong {
  font-weight: 600;
}

a {
  color: var(--color-black);
  text-decoration: none;
  transition: all 0.3s;
}
a:hover {
  color: var(--color-black);
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

ul,
li {
  text-decoration: none;
  padding: 0;
  margin: 0;
  list-style: none;
}

.svg-icon-inline, .svg-icon {
  align-items: center;
  width: 1em;
  height: 1em;
  background-color: transparent;
  fill: currentColor;
}

.svg-icon {
  display: flex;
}

.svg-icon-inline {
  display: inline-flex;
}

@media (max-width: 767px) {
  .hide-mobile {
    display: none !important;
  }
}

@media screen and (min-width: 768px) and (max-width: 991px) {
  .hide-ipad {
    display: none !important;
  }
}

@media (min-width: 992px) {
  .hide-desktop {
    display: none !important;
  }
}

@media (max-width: 991px) {
  .reverse-mobile .columns {
    flex-direction: column-reverse;
  }
}

@media (min-width: 992px) {
  .reverse-desktop .columns {
    flex-direction: row-reverse;
  }
}

@media (min-width: 1280px) {
