  }
}

.header {
  display: flex;
  column-gap: 30px;
  justify-content: space-between;
  padding-top: 30px;
}
.header__brand {
  width: 50%;
}
.header__menu {
  width: 50%;
}

.footer article {
  display: flex;
  column-gap: 30px;
}
.footer__menu {
  border-bottom: 1px solid #cccccc;
  padding: 50px 0 30px;
}
.footer__menu nav ul {
  display: flex;
  flex-wrap: wrap;
  row-gap: 5px;
}
.footer__menu nav ul li {
  font-weight: 500;
  margin-right: 20px;
}
.footer__menu--half {
  width: 50%;
}
.footer__title {
  font-weight: 500;
  display: flex;
  align-items: center;
  padding-bottom: 15px;
}
.footer__title::before {
  display: inline-block;
  content: "";
  height: 6px;
  width: 6px;
  background-image: url("../assets/icons/icon-rombo.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-right: 20px;
}

body {
  margin: 0 auto;
}

#wrapper {
  overflow: hidden;
}

.header__brand {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header__brand figure {
  margin: 0;
}
@media (min-width: 992px) {
  .header__brand figure {
    max-width: 60px;
  }
}
.header__brand figure img {
  width: 100%;
  object-fit: contain;
}
.header__brand .header__breadcrumbs ul {
  display: flex;
  align-items: center;
}
.header__brand .header__breadcrumbs ul li {
  font-weight: 500;
}
.header__brand .header__breadcrumbs ul img {
  height: 10px;
