.btn-animation {
  position: relative;
  font-size: 1.5rem;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  padding-left: 2rem;
}
@media (min-width: 768px) {
  .btn-animation {
    font-size: 1.8rem;
  }
}
.btn-animation:before {
  content: "";
  display: block;
  width: 6rem;
  height: 6rem;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 2rem;
  transform: translateY(-50%);
  margin-left: -2rem;
  background: #131313;
  border-radius: 3rem;
  transition: all 0.3s;
}
.btn-animation:after {
  content: "";
  display: block;
  width: 2.9rem;
  height: 1.7rem;
  background-image: url(../../src/assets/icons/icon-arrow-btn.svg);
  background-size: cover;
  margin-left: 1rem;
}
.btn-animation:hover:before {
  width: calc(100% + 2rem);
}
.btn-animation:hover:after {
  animation-name: btnAnimationArrow;
  animation-duration: 0.3s;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
}

@keyframes btnAnimationArrow {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
